
.cov-message-chat-row {
    position: relative;
    width: 100%;
    overflow: hidden;
}
.cov-message-chat-row.menu-active .cov-message-chat-item {
    transform: translate3d(-9.5rem, 0, 0);
}
.cov-message-chat-row.menu-active .cov-message-menu {
    transform: translate3d(-9.5rem, 0, 0);
}
.cov-message-menu {
    position: absolute;
    top: 0;
    right: -9.5rem;
    background: #ccc;
    width: 9.5rem;
    height: 4rem;
    display: -ms-flexbox;
    display: flex;
    z-index: 1;
    transition: transform .5s;
    transform: translate3d(0, 0, 0);
}
.cov-message-menu-item {
    width: 50%;
    text-align: center;
    line-height: 3rem;
    color: #fff;
}
.cov-message-menu-item.delete {
    background: #ff3737;
}
.cov-message-chat-item {
    display: -ms-flexbox;
    display: flex;
    height: 3rem;
    border-bottom: 1px solid #f0f0f0;
    background: #fff;
    z-index: 2;
    width: 100%;
    transform: translate3d(0, 0, 0);
    transition: transform .5s;
}
.cov-message-chat-avatar {
    background-image: url(../assets/images/cov.jpeg);
    height: 2rem;
    width: 2rem;
    background-size: cover;
    border-radius: 50%;
    margin: .5rem;
    -ms-flex-negative: 0;
        flex-shrink: 0;
}
.cov-message-chat-content {
    padding: 1rem;
    width: 100%;
}
.cov-message-chat-title {
    height: 1rem;
    line-height: 0.8rem;
    font-size: 0.5rem;
}
.cov-message-chat-preview {
    font-size: .5rem;
    color: #666;
    height: 0.8rem;
    overflow: hidden;
}
.cov-message-chat-info {
    -ms-flex-negative: 0;
        flex-shrink: 0;
    font-size: .5rem;
    width: 5rem;
    padding: .8rem .5rem .8rem 0;
    color: #666;
}
.cov-message-chat-content-count-icon {
    background: #ff3737;
    color: #fff;
    padding: 0 .5rem;
    height: 0.7rem;
    border-radius: 0.6rem;
    display: inline-block;
    line-height: 0.7rem;
    box-sizing: border-box;
}
.cov-message-chat-content-time {
    text-align: right;
    margin-bottom: .5rem;
}
.cov-message-chat-content-count {
    text-align: right;
}



