
.cov-view-header {
    top: 0;
    left: 0;
    width: 100%;
    height: 3rem;
    background-color: rgb(38,184,242);
    color: #fff;
    padding: 0 .5rem;
    box-sizing: border-box;
}
.view-container {
    height: calc(100vh - 7rem);
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
}
.cov-view-header-right {
    -ms-flex-negative: 0;
        flex-shrink: 0;
    float:right;
}
.cov-view-header-left {
    -ms-flex-negative: 0;
        flex-shrink: 0;
}
.cov-view-header-center {
    width: 100%;
    text-align: center;
}
.cov-header-avatar {
    background-image: url(../assets/images/cov.jpeg);
    height: 2rem;
    width: 2rem;
    background-size: cover;
    border-radius: 50%;
    margin: .5rem 0;
}
.cov-message-sub-nav-item {
    border: 1px solid #fff;
    height: 1.80rem;
    margin: .6rem;
    width: 4rem;
    line-height: 1.8rem;
    text-align: center;
    display: inline-block;
}
.cov-message-sub-nav-item.left {
    margin-right: 0;
    border-radius: .4rem 0 0 .4rem;
}
.cov-message-sub-nav-item.right{
    margin-left: 0;
    border-left: 0;
    border-radius: 0 .4rem .4rem 0;
}
.cov-message-sub-nav-item.active {
    background-color: #fff;
    color: #26b8f2;
}
.cov-message-sub-nav-plus-icon {
    width: 1.25rem;
    height: 1.25rem;
    margin-top: .875rem;
}
.cov-message-search-box {
    padding: .2rem .4rem;
    background: #fff;
    margin-bottom: -1px;
}
.cov-message-search {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    background-color: #eaeaea;
    height: 2rem;
    border-radius: .4rem;
    width: 100%;
    line-height: 2rem;
    color: #a7a7a7;
    text-align: center;
}
.cov-message-search-icon {
    height: 1rem;
    width: 1rem;
    margin: .45rem .2rem 0 0;
}
.cov-loading {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    color: black;
    text-align: center;
    line-height: 3rem;
}



